<template>
    <div id="app" class="app" ref="app">
        <transition name="router-fade" mode="out-in">
            <head-top v-if="showHeadTop" :search="showHeadTopSearch" :back="showHeadTopBack" :showLogin="showHeadTopLogin"></head-top>
        </transition>
        <transition name="router-fade" mode="out-in">
            <router-view></router-view>
        </transition>
        <foot-nav v-if="showFootNav"></foot-nav>
        <loading v-if="loading"></loading>
    </div>
</template>

<script>
import headTop from 'components/headTop/headTop'
import footNav from 'components/footNav/footNav'
import loading from 'components/loading/loading'
import { mapState } from 'vuex'

export default {
    name: 'app',
    components: {
        'head-top': headTop,
        'foot-nav': footNav,
        'loading': loading,
    },
    computed: mapState([
        'loading',
        'showHeadTop',
        'showHeadTopBack',
        'showHeadTopLogin',
        'showHeadTopSearch',
        'showFootNav',
    ]),
    data() {
        return {
            isShowLoading: true,
        }
    },
    created() {
    }
}

</script>
<style lang="scss" rel="stylesheet/scss">
@import './style/common';
@import './style/mixin';

.app {
    height: 100%;
}

.head_logo {
    left: .2rem;
    @include wh(1.6rem, 1rem);
    @include ct;
    @include bis('images/logo.png');
}

.wrap { 
    height: 100%;
    overflow: auto;
    background: #eee;
}

</style>



